.video {
  display: flex;
  margin: auto;
  padding-top: 120px;
  position: relative;
  padding-bottom: 240px;
  max-width: 1366px;
  justify-content: center;

  @media (max-width: $desktop-width) {
    flex-direction: column;
    margin-top: 53px;
    margin-bottom: 184px;
    padding: 0;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 277px;
  }
}

.video__text-wrapper {
  width: 31.5%;
  margin-left: 57px;
  margin-right: 0.7%;
  padding-right: 71px;

  @media (max-width: $desktop-width) {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    padding-right: 0;
  }

  @media (max-width: $tablet-width) {
    text-align: start;
    padding: 0 15px;
  }
}

.video__text-wrapper h3 {
  font-family: "Bebas Neue", "Arial", monospace;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  margin-top: 42px;
  margin-bottom: 0;

  @media (max-width: $desktop-width) {
    font-size: 42px;
    line-height: 42px;
  }

  @media (max-width: $tablet-width) {
    font-size: 32px;
    line-height: 32px;
    margin-top: 13px;
  }
}

.video__text-wrapper h3 + p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  line-height: 27px;
  margin-top: 12px;
  margin-bottom: 39px;

  @media (max-width: $desktop-width) {
    margin-bottom: 36px;
  }

  @media (max-width: $tablet-width) {
    margin-top: 10px;
    margin-bottom: 24px;
  }
}

.video__text-wrapper p + p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 8px;
  margin-top: 0;

  @media (max-width: $desktop-width) {
    width: 67%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 41px;
  }

  @media (max-width: $tablet-width) {
    width: 92%;
    margin: 0;
    margin-bottom: 32px;
  }
}

.video__text-wrapper span {
  @media (max-width: $desktop-width) {
    display: none;
  }
}

.video__wrap {
  position: absolute;
  bottom: 16.5%;
  right: 50%;
  transform: translateX(61%);

  @media (max-width: $desktop-width) {
    bottom: -13.5%;
    transform: translateX(50%);
  }

  @media (max-width: $tablet-width) {
    bottom: -206px;
  }
}

.video__video-wrapper {
  @media (max-width: $tablet-width) {
    width: 320px;
    height: 167px;
    background-color: $color-gradient-blue;
  }
}

.video__iframe {
  border: none;
  width: 364px;
  height: 228px;

  @media (max-width: $desktop-width) {
    width: 266px;
    height: 167px;
  }

  @media (max-width: $tablet-width) {
    width: 320px;
  }
}

.video__button {
  top: 27%;
  background: transparent;
  border: none;
  position: absolute;
  left: 34%;

  @media (max-width: $desktop-width) {
    top: 50%;
    left: 50%;
    width: 73px;
    height: 73px;
    transform: translate(-50%, -50%);
  }

  @media (max-width: $tablet-width) {
    top: 28%;
    left: 37%;
    transform: translate(0);
  }
}

.video__button:hover svg {
  fill: $color-red;
}

.video__button-play {
  @media (max-width: $desktop-width) {
    width: 73px;
    height: 73px;
  }
}

.video__frame {
  @media (max-width: $desktop-width) {
    width: 266px;
    height: 167px;
  }

  @media (max-width: $tablet-width) {
    width: 320px;
  }
}

.video__img-wrapper {
  @media (max-width: $desktop-width) {
    padding: 0 45px;
  }

  @media (min-width: 555px) and (max-width: 767px) {
    width: 80%;
    margin: 0 auto;
  }

  @media (max-width: $tablet-width) {
    padding: 0;
  }
}

.video__img-wrapper img {
  @media (max-width: $desktop-width) {
    width: 100%;
  }
}

.video__embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}
